// 引入Vue
import Vue from "vue";
// 引入APP
import App from './App.vue'
// 引入Vue-Router
import VueRouter from "vue-router"

// 引入路由
import router from "./router/index"

// 关闭Vue生产提示
Vue.config.productionTip = false;
// 使用VueRouter
Vue.use(VueRouter);

// 创建Vue
new Vue({
    el: "#app",
    render: h => h(App),
    router: router 
})